import { useTranslation } from 'react-i18next';
import { useData } from 'vike-react/useData';
import { Text } from '../../components/ui/text';
import type { PageData } from './+data';
import { Metadata } from '~/components/layout/Metadata';
import { Stack } from 'styled-system/jsx';
import { Table } from '~/components/ui/table';
import { Link } from '~/components/ui/link';

export function Page() {
  const { t } = useTranslation();

  const data: PageData = useData();
  const title = t('title');

  return (
    <>
      <Metadata title={title} helmet />
      <Stack alignItems="center" w="full" _print={{ display: 'none' }}>
        <Text textAlign="center" fontSize="3xl" fontWeight="bold">
          {title}
        </Text>
        <Text textAlign="center">{t('description')}</Text>
        <Table.Root>
          <Table.Head>
            <Table.Row>
              <Table.Header>{t('name')}</Table.Header>
              <Table.Header>{t('unit')}</Table.Header>
            </Table.Row>
          </Table.Head>
          <Table.Body>
            {data.songs.map((d) => {
              return (
                <Table.Row key={d.id}>
                  <Table.Cell fontWeight="medium">
                    <Link href={`/${d.id}`}>{d.title}</Link>
                  </Table.Cell>
                  <Table.Cell>{d.description}</Table.Cell>
                </Table.Row>
              );
            })}
          </Table.Body>
        </Table.Root>
      </Stack>
    </>
  );
}
